.navbar {
    display: flex;
    background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0));
    height: 3em;
    justify-content: center;
    align-items: center;
    box-shadow: none;
}

.navbar ul {
    display: flex;
    list-style: none;
    width: 90vw;
    margin: 0;
    height: 3em;
    align-items: center;
    font-family: 'Quicksand', sans-serif;
}

.navbar li a {
    text-decoration: none;
    margin: 0 1em;
    color: white;
    transition: 0.5s;
}

.navbar li a:hover {
    background: transparent;
    color: black;
    transition: 0.5s;
}

.footer {
    height: 3em;
    background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0));
    color: #FFF;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Quicksand', sans-serif;
    font-weight: lighter;
}

